import React from 'react'
import { app, StoreWrap } from 'xadmin-core'

import i18n from 'xadmin-i18n'

@StoreWrap('hello')
class Hello extends React.Component {
  render() {
    const { count, add } = this.props
    const { _t } = app.context
    return (
      <div>
        {count} <a onClick={add}>{_t('Change')}</a>
      </div>
    )
  }
}

app
.use(i18n)
.use({
  config: {
    locale: {
      lng: 'zh_Hans'
    }
  },
  mappers: {
    hello: {
      data: ({ state }, props ) => {
        return { count: state.count }
      },
      method: {
        add: ({ dispatch }) => () => {
          dispatch({ type: 'ADD' })
        }
      }
    }
  },
  reducers: {
    count: (state=0, action) => {
      if(action.type == 'ADD') {
        return state + 1
      }
      return state
    }
  },
  routers: {
    '@': {
      path: '/',
      component: Hello 
    }
  }
}).start({ container: '#app' })



